import React, { Suspense, useRef, useEffect } from "react";
import { IS_THEME, PostTrimContent, themeImpressionTracking } from '../../../../utils'
import { isSocialWall, isTagEmbed, isTagShop, isTagbox } from "../../../../actions/api";
import { suiteTrack } from '../../../../actions/api'
const ShareOnHover = React.lazy(() => import("../../Elements/ShareOnHover/ShareOnHover"));
const Network = React.lazy(() => import("../../Elements/Network/Network"));
const ShopButton = React.lazy(() => import("../../Elements/ShopButton/ShopButton"));
const AuthorDetails = React.lazy(() => import("./AuthorDetails"));
const Media = React.lazy(() => import('./Media'));
const PostContent = React.lazy(() => import('../../Elements/PostContent/PostContent'));
const trim_content_length = !isTagEmbed ? 200 : null;


const Card = ({ itemData, personalization, adjustWidth, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, postSizeInPx, wall, onClickPopUpSlider }) => {
  const postTrack = useRef(null);
  const mediaType = itemData.type !== 1 ? true : false;
  const postTextContent = personalization.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content
  const responsiveCardSize2x = postSizeInPx.adjustWidth < 250 ? true : false
  const themeMode = (ThemeRule.postHover == 0 && isTagShop) ? 'light' : 'dark'
  const overlay = isTagbox ? 'tb_sp_color_overlay' : 'tb_sp_post_overlay-' + themeMode
  const overlayStyle = { backgroundColor: isTagbox ? ThemeRule.cardColor : '' }
  useEffect(() => {
    if (suiteTrack || isTagShop) themeImpressionTracking(postTrack);
  }, []);
  const isHotspot = itemData.hotspot && itemData.ugc_products.length > 0 ? true : false
  const isShopBtn = isTagShop && ThemeRule.shoppingText != '' && (!itemData.hotspot && itemData.ugc_products.length > 0) ? true : false;



  useEffect(() => {
    if (itemData.isPopUp) onClickPopUpSlider(itemIndex, itemData)
  }, [itemIndex, itemData.isPopUp, itemData]);
  return <div id={`tb-sp-post-${itemData.id}`} className={`tb_sp_post_wrapper${responsiveCardSize2x ? ' tb_sp_post_wrapper2x' : ''}`} style={{ width: `${adjustWidth}%`, padding: personalization.padding / 2 }} tb-network={itemData.network.id} tb-product={isShopBtn ? 1 : isHotspot ? 2 : 0} tabIndex="0" role="article" aria-label={`Post ${parseInt(itemIndex) + 1},${postTextContent}`} ref={postTrack} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId}>
    <div className={`tb_sp_post_in${isTagShop ? ' tb_sp_post_ani' : ''}`} onClick={clickToShowPopUp(itemIndex, itemData)} role="button" aria-roledescription="Click to open pop up" tabIndex={"0"}>
      <div className="tb_sp_post_media_wrapp" role="button" tabIndex="0" aria-label="Pop Up Button">
        {itemData.share.status ? <Suspense fallback={<></>}><ShareOnHover share={itemData.share} shareClass={'tb_sp_share_container'} item={itemData} wallID={wallID} ownerId={ownerId} /></Suspense> : ''}
        {mediaType ? <Suspense fallback={<></>}><Media key={`img${itemIndex}_${itemData.id}`} itemData={itemData} wallID={wallID} ownerId={ownerId} ThemeID={personalization.widgetTheme} wall={wall} /></Suspense> : ''}
        {!isHotspot ? <>
          <div className="tb_sp_post_details">
            {isTagShop ? <div className="tb_sp_social_">
              <Suspense fallback={<></>}><Network networkClass={'tb_sp_social__ico'} network={itemData.network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} Personalization={personalization} /></Suspense>
            </div> : null}
            {isShopBtn ? <Suspense fallback={<></>}><ShopButton ThemeRule={ThemeRule} btnClass={'tb_sp_btn_wrap'} /></Suspense> : null}
            {!isTagShop ? <>
              { !IS_THEME(65) ? <div className="tb_sp_contant_wrapper">
                {((!ThemeRule.hideContent || itemData.type == 1)) ? <Suspense fallback={<></>}><PostContent contentClass={'tb_sp_content'} item={itemData} content={postTextContent} font={ThemeRule} ThemeRule={ThemeRule} personalization={personalization} contentTitle={itemData.contentTitle} /></Suspense> : ''}
              </div> : ''}
           <Suspense fallback={<></>}><AuthorDetails ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} /></Suspense>
            </>
              : null}
          </div>
          <div className={`tb_sp_post_overlay ${overlay}`} style={overlayStyle}><div></div></div>
        </> : null}
      </div>
    </div>
  </div>
}
export default Card